body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  background: linear-gradient(to right, #00C9FF, #92FE9D);
}

h1 {
  margin: 0;
  margin-bottom: 0.8em;
  color: rgba(56, 56, 56, 0.8);
}

.input {
  font-size: 1.2rem;
  padding: 0.3rem;
  border: 2px solid #777;
  border-radius: 0.3em;
}

select {
  margin-left: 1.5em;
  font-size: 1.2rem;
  padding: 0.3rem;
  border: 2px solid #777;
  border-radius: 0.3em;
}

.equal {
  font-size: 2.2rem;
  font-weight: bold;
  color: rgba(56, 56, 56, 0.8);
}
